import React, { Component } from 'react';

import HeaderPoint from './HeaderPoint/HeaderPoint';
import logo from '../assets/logo.png';

import './Header.css';

class Header extends Component {
    render() {
        return (
            <div className="header">
                <a className="homeButton" href="/">
                    <img src={logo} alt="Logo" className="logo"></img>
                    <div className="logoName titleText">
                        Al Capone Barbershop
                    </div>
                </a>

                <HeaderPoint text="Öffnungszeiten" idScrollTo="oeffnungszeiten"/>
                <HeaderPoint text="Kontakt & ankunft" idScrollTo="kontaktAnkunft"/>
                <HeaderPoint text="Angebot" idScrollTo="angebot"/>
                <HeaderPoint text="Tour" idScrollTo="tour"/>
                <HeaderPoint text="Über uns" idScrollTo="ueberuns"/>
            </div>
        );
    }
}

export default Header;
