import React, { Component } from 'react';

import {Player} from 'video-react';
import "video-react/dist/video-react.css";

import create360Viewer from '360-image-viewer';

import './Tour.css';
import TourVideo from '../../assets/TourVideo NEW.mp4';
import Logo from '../../assets/logo.png';
import Panorama from '../../assets/panorama.jpg';

class Tour extends Component {



    render() {
        let panoramaImg = new Image();
        panoramaImg.src = Panorama;
        panoramaImg.onload = () => {
            const viewer = create360Viewer({
                image: panoramaImg
            });
            document.getElementById("vrview").appendChild(viewer.canvas);

            //const fit = canvasFit(viewer.canvas, document.getElementById("vrview")., window.devicePixelRatio);
            //window.addEventListener('resize', fit, false);
            //fit();
            const fit = () => {
                var w = window.innerWidth
                    || document.documentElement.clientWidth
                    || document.body.clientWidth;

                viewer.canvas.width = w * 0.84;
                viewer.canvas.height = w * 0.84 * 0.5;
            }
            window.addEventListener('resize', fit, false);
            fit();
            viewer.start();
        };
    
        return (
            <div className="tourWrapper">
                <div className="videoWrapper">
                    <Player src={TourVideo} poster={Logo} aspectRatio="16:9">
                    </Player>
                </div>
                <div clasName="panoramaWrapper">
                    <div id="vrview"></div>
                </div>
            </div>
        );
    }
}

export default Tour;
