import React, { Component } from 'react';

import './HeaderPoint.css';

/**
 * @param text
 * @param idScrollTo
 */
class HeaderPoint extends Component {
    render() {
        return (
            <div className="headerPoint">
                <a href={"#" + this.props.idScrollTo} className="navPoint">
                    {this.props.text}
                </a>
            </div>
        );
    }
}

export default HeaderPoint;
